import { getType } from 'typesafe-actions';
import { fetchProvidersFailure, fetchProvidersRequest, fetchProvidersSuccess } from './providersActions';
export const defaultState = {
    byId: new Map(),
    errors: new Map(),
    fetchStatus: new Map(),
};
export function providersReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(fetchProvidersRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.reportId, 1 /* inProgress */) });
        case getType(fetchProvidersSuccess):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.reportId, Object.assign({}, action.payload)), errors: new Map(state.errors).set(action.meta.reportId, null) });
        case getType(fetchProvidersFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.reportId, action.payload) });
        default:
            return state;
    }
}
